body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

form {
  margin: 20px;
}
label {
  display: block;
}
input {
  margin-left: 20px;
  margin-top: 20px;
}
input[type="checkbox"] {
  margin-left: 0;
  margin-right: 10px;
}
button {
  display: block;
  max-width: 200px;
  padding: 10px 20px;
  margin-top: 20px;
}
span {
  color: red;
  font-size: 10px;
  margin-left: 10px;
}

